.glassFigure {
    position: absolute;
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    // min-height: 100vh;
    background: transparent;
    width: 150px;
    height: 150px;
    top: 210px;
    right: 495px;
    transform: scale(2);

    span {
        position: absolute;
        width: 100%;
        height: 100%;
        border-radius: 50%;
        backdrop-filter: blur(10px);
        backface-visibility: hidden;
    }
}

.glassFigure span:nth-child(1) {
    background: rgba(0, 0, 0, 0.1);
    border-bottom: 0.5px solid rgba(158, 240, 26, 0.5);
    border-left: 0.5px solid rgba(158, 240, 26, 0.5);
    // border-bottom: 0.25px solid #fff;
    // border-right: 0.25px solid #fff;
    z-index: 2;
    animation: clockwise 2s infinite;
    animation-delay: -2s;
}
.glassFigure span:nth-child(2) {
    backdrop-filter: blur(10px);
    position: absolute;
    background: rgba(158, 240, 26);
    z-index: 2;
    border-radius: 0%;
    // box-shadow: 6px 6px 10px #88888866, -6px -6px 10px #ffffff88;
    transform: scale(0.6);
    animation: animate 2s ease-in-out infinite,
    
}
