@keyframes animate {
    0% {
        transform: translate(-100px, -90px) scale(0.4) rotate(0deg);
        z-index: 1;
        box-shadow: 0px 0px 10px rgb(81, 255, 58), 0 0 20px rgb(255, 255, 255),
            0 0 40px rgb(255, 255, 255), 0 0 80px rgb(255, 255, 255),
            0 0 160px rgb(87, 255, 58);
    }
    50% {
        transform: translate(150px, 90px) scale(0.4) rotate(180deg);
        z-index: 1;
        box-shadow: 0px 0px 10px rgb(21, 254, 4), 0 0 20px rgb(255, 255, 255),
            0 0 40px #fff, 0 0 80px #fff, 0 0 160px rgb(58, 255, 58);
    }
    50.00001% {
        transform: translate(150px, 90px) scale(0.4) rotate(180deg);
        z-index: 3;
        box-shadow: 0px 0px 10px rgb(3, 252, 32), 0 0 20px rgb(255, 255, 255),
            0 0 40px #fff, 0 0 80px #fff, 0 0 160px rgb(58, 255, 94);
    }
    100% {
        transform: translate(-100px, -90px) scale(0.4) rotate(360deg);
        z-index: 3;
        box-shadow: 0px 0px 10px rgb(9, 255, 0), 0 0 20px rgb(255, 255, 255),
            0 0 40px #fff, 0 0 80px #fff, 0 0 160px rgb(58, 255, 64);
    }
}

// animate clockwise border-bottom and border-left
@keyframes clockwise {
    0% {
        transform: rotate(20deg);
    }
    60% {
        transform: rotate(230deg);
    }
    100% {
        transform: rotate(380deg);
    }
}

@media screen and (max-width: 600px) {
    .glassFigure {
        position: absolute;
        top: 73px;
        left: 60px;
        
        transform: scale(1.3);
    }
}
