.navigation {
    display: flex;
    justify-content: center;
    align-items: center;
    // min-height: 100vh;
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 2.5rem;
}

#footer {
    bottom: 10px;
    width: 100%;
    height: 60px;
}

//  list view

.navigation ul {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 10px;
    box-shadow: 0 5px 25px rgba(43, 5, 97, 0.755);
    list-style: none;
    margin-bottom: 15px;
}

// each li view

.navigation ul li a {
    position: relative;
    color: rgb(255, 202, 58);
    text-decoration: none;
    display: inline-block;
    padding: 10px 20px;
    text-align: justify;
    z-index: 1000;
    backdrop-filter: blur(16px);
    -webkit-backdrop-filter: blur(16px);

    &:hover:not(.active) {
        transform: scale(1.02);
        transition: 0.2s;
    }

    .text {
        font-size: 0.75em;
        color: #fff;
        opacity: 0;
        transform: translateY(10px);
        transition: 0.5s;
        display: flex;
    }
}

ul li a.active {
    transform: translateY(-5px);
    height: 75px;

    .text {
        opacity: 1;
        transform: translateY(9px);
        transition: 0.9s;
    }
}

ul li a svg {
    font-size: 30px;
    // pointer-events: none;
    opacity: 0.4;
    transition: 0.25s;
    // align-items: center;
    // justify-content: center;
    // display: flex;
    width: 100%;
    height: 100%;
    color: rgb(255, 202, 58);

}

ul li a.active svg {
    transition: 0.5s;
    opacity: 1;
    transform: scale(1.3);
    transform: translateY(-5px), scale(1.1);
}

#marker {
    position: absolute;
    top: 0;
    transform: translateX(50%);

    width: 41px;
    height: 41px;
    transition: 0.5s;
    z-index: -1;
}

#marker::before {
    content: '';
    position: absolute;
    top: -12px;
    // left: 20%;
    transform: translateX(-50%);
    width: 41px;
    height: 35px;

    border-radius: 8px;
    background: rgb(255, 255, 255, .7);
    box-shadow: 0 0 10px rgb(106, 76, 147), 0 0 20px rgb(255, 255, 255),
        0 0 40px #fff, 0 0 80px #fff, 0 0 160px rgb(106, 76, 147);

    
}

@keyframes animateGlow {
    0% {
        background-position: 0% 50%;
    }
    100% {
        background-position: 200% 50%;
    }
}

