$border-radius-size: 14px;
$barbarian: #ec9b3b;

*,
*:before,
*:after {
  box-sizing: border-box;
}

.slide-container {
  margin: auto;
  width: 600px;
  text-align: center;
}
.wrapper {
  padding-top: 4px;
  padding-bottom: 4px;

  &:focus {
    outline: 0;
  }
}

.clash-card {
  background: rgba(106, 76, 147, 0.6);
  width: 300px;
  display: inline-block;
  top: 450px;
  border-radius: $border-radius-size + 5;
  position: relative;
  text-align: center;
  box-shadow: -1px 15px 30px -12px black;
  z-index: 9999;
  animation: glow 1s ease-in-out infinite alternate;
  backface-visibility: hidden;

  &:hover {
    transform: scale(1.1);
    animation: pulse 1s ease-in-out infinite alternate,
      glow 1s ease-in-out infinite;
    transition: all 0.7s ease-in-out;
  }
}

.clash-card__image {
  position: relative;
  height: 230px;
  margin-bottom: 35px;
  border-top-left-radius: $border-radius-size;
  border-top-right-radius: $border-radius-size;
}
.clash-card__image--barbarian {
  backdrop-filter: blur(10px);
  background-color: rgba(254, 0, 0, 0.1);
  //   background: url('https://i.imgur.com/HMkoRPR.png');
  img {
    width: 298px;
    position: absolute;
    top: -72px;
    left: 0px;
  }
}

.clash-card__level {
  text-transform: uppercase;
  font-size: 12px;
  font-weight: 700;
  margin-bottom: 3px;
}

.clash-card__level--barbarian {
  color: $barbarian;
}
.clash-card__unit-name {
  font-size: 26px;
  color: white;
  font-weight: 900;
  margin-bottom: 5px;
}

.clash-card__unit-description {
  padding: 20px;
  margin-bottom: 10px;
  color: white;
}

.clash-card__unit-stats--barbarian {
  background: $barbarian;

  .one-third {
    border-right: 1px solid #bd7c2f;
  }
}

.clash-card__unit-stats {
  color: white;
  font-weight: 700;
  border-bottom-left-radius: $border-radius-size;
  border-bottom-right-radius: $border-radius-size;

  .one-third {
    width: 33%;
    float: left;
    padding: 20px 15px;
  }

  sup {
    position: absolute;
    bottom: 4px;
    font-size: 45%;
    margin-left: 2px;
  }

  .stat {
    position: relative;
    font-size: 24px;
    margin-bottom: 10px;
  }

  .stat-value {
    text-transform: uppercase;
    font-weight: 400;
    font-size: 12px;
  }

  .no-border {
    border-right: none;
  }
}

.clearfix:after {
  visibility: hidden;
  display: block;
  font-size: 0;
  content: ' ';
  clear: both;
  height: 0;
}

// create animated gradient underglow for the card
@keyframes underglow {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

// create a pulsing animation for the card
@keyframes pulse {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.1);
  }
  100% {
    transform: scale(1);
  }
}
// add animated gradient glow effect to the card
@keyframes glow {
  0% {
    box-shadow: 0 0 20px 0px rgba(236, 59, 94, 0.5);
  }
  50% {
    box-shadow: 0 0 20px 10px rgba(13, 27, 225, 0.6);
  }
  100% {
    box-shadow: 0 0 20px 0px rgba(46, 225, 19, 0.5);
  }
}

@media (max-width: 600px) {
  .clash-card {
    top: 35px;
    left: -105px;
    transform: scale(.8);
  }
}
