.contact-form {
  width: 40%;
  margin-top: 20px;

  ul {
    padding: 0;
    margin: 0;
    background: rgba(1, 49, 85, 0.3);
    backdrop-filter: blur(10px);
    border-radius: 15px;
    animation: smooth 3s ease-in;
    li {
      padding: 0px;
      margin: 0;
      list-style: none;
      margin-bottom: 10px;
      opacity: 0;
      overflow: hidden;
      display: block;
      clear: both;
      position: relative;
      animation: fadeIn 1s 1s forwards;
    }

    li.half {
      height: 49%;
      width: 49%;
      margin-left: 2%;
      float: left;
      clear: none;

      &:first-child {
        margin-left: 0;
      }
    }
  }

  input[type='text'],
  input[type='email'] {
    width: 100%;
    border: 0;
    background: #ffffff;
    height: 50px;
    font-size: 16px;
    color: #fff;
    padding: 0 20px;
    box-sizing: border-box;
  }

  textarea {
    width: 100%;
    border: 0;
    background: #ffffff;
    height: 50px;
    font-size: 16px;
    color: #fff;
    padding: 20px;
    box-sizing: border-box;
    min-height: 150px;
  }

  .flat-button {
    color: #ffd700;
    font-size: 11px;
    letter-spacing: 3px;
    text-decoration: none;
    padding: 8px 10px;
    border: 1px solid #ffd700;
    float: left;
    border-radius: 4px;
    background: 0 0;
    text-transform: uppercase;
    float: right;
    text-align: center;
    // margin-right: 10px;
  }
}

@keyframes smooth {
  0% { opacity: 0;}
  100% { opacity: 1;}
}

