* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}
body {
    background-color: rgb(106, 76, 147);
}

.page {
    width: 100%;
    height: 100%;
    position: absolute;
}

.top-tags {
    bottom: auto;
    top: 35px;
}

.tags {
    color: #050505;
    opacity: 0.6;
    position: absolute;
    bottom: 0;
    left: 30px;
    font-size: 18px;
    font-family: 'La Belle Aurore', cursive;
}

.bottom-tag-html {
    margin-left: -20px;
}

.container {
    width: 100%;
    will-change: contents;
    height: 90%;
    min-height: 566px;
    position: absolute;
    opacity: 0;
    top: 5%;
    margin: 0 auto;
    z-index: 1;
    transform-style: preserve-3d;
    animation: fadeIn 1s forwards;
    animation-delay: 1s;
}

.container.contact-page,
.container.about-page,
.container.portfolio-page {
    h1 {
        font-size: 53px;
        font-family: 'Coolvetica';
        color: #ffffff;
        font-weight: 400;
        margin-top: 0;
        position: relative;
        margin-bottom: 20px;
        left: -10px;


        &:before {
            content: '<h1>';
            font-family: 'La Belle Aurore', cursive;
            color: #a2a29b;
            font-size: 18px;
            position: absolute;
            margin-top: -10px;
            left: -10px;
            opacity: 0.6;
            line-height: 18px;
        }

        &:after {
            content: '<h1/>';
            font-family: 'La Belle Aurore', cursive;
            color: #9d9e96;
            font-size: 18px;
            line-height: 18px;
            position: absolute;
            left: -30px;
            bottom: -20px;
            margin-left: 20px;
            opacity: 0.6;
        }
    }

    p {
        font-size: 25px;
        color: #fff;
        font-family: sans-serif;
        font-weight: 300;
        animation: pulse 1s;
        &:nth-of-type(1) {
            animation-delay: 1.1s;
        }
        &:nth-of-type(2) {
            animation-delay: 1.2s;
        }
        &:nth-of-type(3) {
            animation-delay: 1.3s;
        }
    }

    .text-zone {
        position: absolute;
        left: 10%;
        top: 50%;
        transform: translateY(-30%);
        max-width: 95%;
        vertical-align: middle;
        display: table-cell;
        max-height: 90%;
    }

    .text-animate-hover {
        &:hover {
            color: yellow;
        }
    }
}

@media (max-width: 768px) {
    .container {
        p {
            padding: 5px;
            margin-left: -15px;
            width: 97%;
        }
    }
}
.bottom-tag-html {
    opacity: 0;
}
.top-tags {
    opacity: 0;
}
.container.contact-page .text-zone p {
    font-size: 15px;
    // background: rgba(5, 5, 5, 0.7);
    border-radius: 10px;
}
.contact-form {
    margin: -5px;
}