.home-page {
    .text-zone {
        backface-visibility: hidden;
        position: absolute;
        left: 9%;
        top: 50%;
        transform: translateY(-50%);
        width: 90%;
        max-height: 90%;
        padding-left: 50px;
        color: aliceblue;
    }

    h1 {
        backface-visibility: hidden;
        text-overflow: unset;
        // color: white;
        font-size: 60px;
        margin: 0px;
        max-width: fit-content;
        font-weight: 400;
        font-family: 'Helvetica Neue', Helvetica, Arial, sans-serif;
        // background-color: rgba(255, 255, 255, 0.5);
        border-radius: 10px;

        &::before {
            content: '<h1>';
            font-family: 'La Belle Aurore', cursive;
            color: rgb(5, 5, 5);
            font-size: 20px;
            position: absolute;
            left: 5px;
            margin-top: -15px;
            animation: fadeIn 1.5s 1.7s backwards;
            opacity: 0.2;
        }

        &::after {
            content: '</h1>';
            font-family: 'La Belle Aurore', cursive;
            color: rgb(0, 0, 0);
            font-size: 20px;
            position: absolute;
            left: 5px;
            margin-top: -15px;
            animation: fadeIn 1.5s 1.7s backwards;
            opacity: 0.2;
        }
    }
}

h2 {
    // color: whitesmoke;
    margin-top: 20px;
    font-size: 20px;
    font-weight: 400;
    font-family: 'Helvetica Neue', Helvetica, Arial, sans-serif;
    letter-spacing: 1px;
    animation: fadeIn 1.3s 1.1s backwards;
}

.home-page .text-zone .flat-button {
    backface-visibility: hidden;
    opacity: 0.4;
    color: rgb(237, 233, 233);
    font-size: 15px;
    font-weight: 400;
    letter-spacing: 4px;
    font-family: 'Roboto';
    text-decoration: none;
    width: max-content;
    background: linear-gradient(90deg, yellow 50%, transparent 50%),
        linear-gradient(90deg, yellow 50%, transparent 50%),
        linear-gradient(0deg, yellow 50%, transparent 50%),
        linear-gradient(0deg, yellow 50%, transparent 50%);
    background-repeat: repeat-x, repeat-x, repeat-y, repeat-y;
    background-size: 15px 1px, 15px 1px, 1px 15px, 1px 15px;
    padding: 10px 18px;
    animation: border-dance 3.2s infinite alternate backwards,
        fadeIn 3s 3s backwards;
    white-space: nowrap;
    // margin-left: 95px;
    position: absolute;
    margin-top: 22px;

    &:hover {
        background: yellow;
        color: #000000;
    }
}
@keyframes border-dance {
    0% {
        background-position: 0 0, 50px 100%, 0 50px, 100% 0;
    }
    100% {
        background-position: 50px 0, 0 100%, 0 0, 100% 50px;
    }
    0% {
        opacity: 0;
    }
    80% {
        opacity: 1;
    }
    100% {
        opacity: 0.5;
    }
}

@media screen and (max-width: 768px) {
    .text-zone {
        left: 5%;
        padding-left: 0px;
        h1 {
            font-size: 34px;
            max-width: 85%
        }
        h2 {
            font-size: 15px;
        }
        .flat-button {
            margin-left: 0px;
        }
    }
}
